@import url('https://fonts.googleapis.com/css?family=Lato');

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}
.container{
  margin: 0 auto;
  max-width: 1170px;
}
.background{  
  background: #6441A5;
  background: linear-gradient(to right, #290044, #6441a5)
}
header{  
  margin: 0 auto;
  max-width: 1170px;
  height: 100px;
  display: flex;
  align-items: center;
}
header > img{
  height: 88%;
  margin-left: 10%;
}
header > p{
  color:whitesmoke;
  font-size: 50px;
  margin-left: 60px;
}
.meme{
  position: relative;
  width: 60%;
  margin: 0 auto;
}
.meme >img{
  width: 100%;
}
.meme >h2{
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-size: 3em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0 #000;
}
.meme > .bottom{
  bottom: 0;
}
.meme > .top{
  top: 0;
}
.meme-form{
  width: 90%;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
}
.meme-form > input{
  width: 45%;
  height: 40px;
}
.meme-form > button{
  border: none;
  font-size: 25px;
  color:white;
  background: #6441a5;
}
.meme-form >input::-webkit-input-placeholder{
  font-size: 25px;
  text-align: center;
}
.meme-form >input:-moz-placeholder{
  font-size: 25px;
  text-align: center;
}